'use client';

import React, { useEffect, useRef, useState } from 'react';

interface UserMarker {
  id: string;
  coordinates: [number, number];
  timestamp: Date;
}

export default function Map() {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<any>(null);
  const userMarkersRef = useRef<any[]>([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [userMarkers, setUserMarkers] = useState<UserMarker[]>([]);
  const [isAddingMarker, setIsAddingMarker] = useState(false);

  useEffect(() => {
    // Load Leaflet CSS
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/leaflet.min.css';
    document.head.appendChild(link);

    // Load Leaflet JS
    const script = document.createElement('script');
    script.src = 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/leaflet.min.js';
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.body.appendChild(script);

    return () => {
      document.head.removeChild(link);
      document.body.removeChild(script);
      if (mapInstanceRef.current) {
        mapInstanceRef.current.remove();
      }
    };
  }, []);

  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;

    const L = (window as any).L;

    // Initialize map centered on Madison, Wisconsin
    const map = L.map(mapRef.current).setView([43.0731, -89.4012], 13);

    // Add OpenStreetMap tiles
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '¬© OpenStreetMap contributors',
      maxZoom: 19,
    }).addTo(map);

    // Add a default blue marker
    const marker = L.marker([43.0731, -89.4012]).addTo(map);
    marker.bindPopup('<b>Madison, Wisconsin</b><br>Welcome!').openPopup();

    // Custom red icon for user markers
    const redIcon = L.icon({
      iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png',
      shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
      iconSize: [25, 41],
      iconAnchor: [12, 41],
      popupAnchor: [1, -34],
      shadowSize: [41, 41]
    });

    // Handle map clicks to add user markers
    map.on('click', (e: any) => {
      if (!isAddingMarker) return;

      const { lat, lng } = e.latlng;
      const markerId = `marker-${Date.now()}`;

      // Create red marker
      const userMarker = L.marker([lat, lng], { icon: redIcon }).addTo(map);
      
      // Add popup with delete option
      userMarker.bindPopup(`
        <div style="text-align: center;">
          <p style="margin: 0 0 8px 0;"><b>Your Marker</b></p>
          <p style="margin: 0 0 8px 0; font-size: 12px;">
            ${lat.toFixed(4)}, ${lng.toFixed(4)}
          </p>
          <button 
            onclick="window.deleteMarker('${markerId}')"
            style="background-color: #EF4444; color: white; padding: 4px 12px; border: none; border-radius: 4px; cursor: pointer; font-size: 12px;"
          >
            Delete Marker
          </button>
        </div>
      `);

      // Store marker reference with ID
      userMarker._customId = markerId;
      userMarkersRef.current.push(userMarker);

      // Update state
      setUserMarkers(prev => [...prev, {
        id: markerId,
        coordinates: [lat, lng],
        timestamp: new Date()
      }]);
    });

    mapInstanceRef.current = map;
  }, [isLoaded, isAddingMarker]);

  // Global function to delete markers
  useEffect(() => {
    (window as any).deleteMarker = (markerId: string) => {
      const L = (window as any).L;
      
      // Find and remove the marker from map
      const markerToRemove = userMarkersRef.current.find(m => m._customId === markerId);
      if (markerToRemove && mapInstanceRef.current) {
        mapInstanceRef.current.removeLayer(markerToRemove);
        userMarkersRef.current = userMarkersRef.current.filter(m => m._customId !== markerId);
      }

      // Update state
      setUserMarkers(prev => prev.filter(m => m.id !== markerId));
    };

    return () => {
      delete (window as any).deleteMarker;
    };
  }, []);

  const toggleAddingMode = () => {
    setIsAddingMarker(!isAddingMarker);
  };

  const clearAllMarkers = () => {
    if (!mapInstanceRef.current) return;

    // Remove all user markers from map
    userMarkersRef.current.forEach(marker => {
      mapInstanceRef.current.removeLayer(marker);
    });

    userMarkersRef.current = [];
    setUserMarkers([]);
  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col">
      <header className="bg-blue-600 text-white p-4 shadow-lg">
        <h1 className="text-3xl font-bold">Interactive Map with Custom Markers</h1>
        <p className="text-blue-100 mt-1">Click the button below, then click on the map to add red markers</p>
      </header>
      
      <main className="flex-1 p-4">
        <div className="max-w-6xl mx-auto">
          {/* Control Panel */}
          <div className="bg-white rounded-lg shadow-lg p-4 mb-4 flex items-center justify-between">
            <div className="flex gap-3">
              <button
                onClick={toggleAddingMode}
                className={`px-6 py-2 rounded-lg font-semibold transition ${
                  isAddingMarker 
                    ? 'bg-red-500 text-white hover:bg-red-600' 
                    : 'bg-green-500 text-white hover:bg-green-600'
                }`}
              >
                {isAddingMarker ? 'üî¥ Stop Adding Markers' : 'üìç Start Adding Markers'}
              </button>
              
              {userMarkers.length > 0 && (
                <button
                  onClick={clearAllMarkers}
                  className="px-6 py-2 bg-gray-500 text-white rounded-lg font-semibold hover:bg-gray-600 transition"
                >
                  üóëÔ∏è Clear All ({userMarkers.length})
                </button>
              )}
            </div>

            <div className="text-sm text-gray-600">
              {isAddingMarker ? (
                <span className="text-red-600 font-semibold">‚úì Click anywhere on map to add marker</span>
              ) : (
                <span>Click "Start Adding Markers" to begin</span>
              )}
            </div>
          </div>

          {/* Map */}
          <div className="bg-white rounded-lg shadow-lg overflow-hidden">
            <div 
              ref={mapRef} 
              className={`w-full h-[600px] ${isAddingMarker ? 'cursor-crosshair' : ''}`}
              style={{ minHeight: '600px' }}
            />
          </div>

          {/* Markers List */}
          {userMarkers.length > 0 && (
            <div className="mt-4 bg-white rounded-lg shadow p-4">
              <h2 className="text-xl font-semibold mb-3">Your Markers ({userMarkers.length})</h2>
              <div className="space-y-2">
                {userMarkers.map((marker, index) => (
                  <div 
                    key={marker.id}
                    className="flex items-center justify-between p-3 bg-gray-50 rounded hover:bg-gray-100 transition"
                  >
                    <div>
                      <span className="font-semibold text-gray-900">Marker {index + 1}</span>
                      <p className="text-sm text-gray-600">
                        üìç {marker.coordinates[0].toFixed(4)}, {marker.coordinates[1].toFixed(4)}
                      </p>
                      <p className="text-xs text-gray-500">
                        Added: {marker.timestamp.toLocaleTimeString()}
                      </p>
                    </div>
                    <button
                      onClick={() => (window as any).deleteMarker(marker.id)}
                      className="px-4 py-2 bg-red-500 text-white rounded hover:bg-red-600 transition text-sm"
                    >
                      Delete
                    </button>
                  </div>
                ))}
              </div>
            </div>
          )}
          
          <div className="mt-4 bg-white rounded-lg shadow p-4">
            <h2 className="text-xl font-semibold mb-2">How to Use</h2>
            <ul className="text-gray-700 space-y-2">
              <li>1. Click <strong>"Start Adding Markers"</strong> button</li>
              <li>2. Click anywhere on the map to place a red marker</li>
              <li>3. Click on a marker to see details and delete option</li>
              <li>4. Click <strong>"Stop Adding Markers"</strong> when done</li>
              <li>5. Use <strong>"Clear All"</strong> to remove all markers at once</li>
            </ul>
          </div>
        </div>
      </main>
    </div>
  );
}